.content {
	padding: 30px 0;
	.titleBig {
		text-align: center;
		font-family: 'Source Sans Pro' , sans-serif;
		font-size: 30px;
		font-weight: 400;
		color: #000;
		text-transform: uppercase;
		margin-bottom: 30px; }
	.navbar {
		width: 100%;
		float: left;
		clear: both;
		margin-bottom: 25px;
		ul {
			width: 100%;
			display: flex;
			justify-content: space-around; }
		li {
			float: left;
			display: block;
			list-style: none;
			a {
				font-family: 'Roboto' , sans-serif;
				color: #34476b;
				font-size: 16px;
				font-weight: 300;
				text-decoration: none; }
			&.ui-state-active {
				a {
					color: #000 !important; } }
			& {
				outline: 0 !important;
				border: 0 !important;
				a {
					outline: 0 !important;
					border: 0 !important; } } } }
	.sectors {
		width: 100%;
		background-color: #f8f8f8;
		float: left;
		box-sizing: border-box;
		padding: 25px 30px 35px 25px;
		.title {
			font-family: 'Source Sans Pro' , sans-serif;
			font-size: 30px;
			font-weight: 400;
			color: #000;
			text-transform: uppercase;
			margin-bottom: 25px; }
		.longField {
			width: 340px;
			float: left;
			margin-right: 30px;
			margin-bottom: 20px;
			label {
				font-family: 'Roboto' , sans-serif;
				font-size: 14px;
				color: #000;
				font-weight: 700;
				float: left;
				clear: both;
				display: block;
				width: 100%;
				margin-bottom: 5px; }
			input {
				display: block;
				width: 100%;
				box-sizing: border-box;
				height: 40px;
				border: 1px solid #e9e9e9;
				display: block;
				box-sizing: border-box;
				padding: 4px 5px; } }
		.smallField {
			width: 110px;
			float: left;
			margin-right: 9px;
			margin-bottom: 20px;
			&:last-child {
				margin-right: 0; }
			label {
				font-family: 'Roboto' , sans-serif;
				font-size: 14px;
				color: #000;
				font-weight: 700;
				float: left;
				clear: both;
				display: block;
				width: 100%;
				margin-bottom: 5px; }
			input {
				display: block;
				width: 100%;
				box-sizing: border-box;
				height: 40px;
				border: 1px solid #e9e9e9;
				display: block;
				box-sizing: border-box;
				padding: 4px 5px; } }
		.veryLongField {
			width: 100%;
			display: block;
			float: left;
			clear: both;
			label {
				font-family: 'Roboto' , sans-serif;
				font-size: 14px;
				color: #000;
				font-weight: 700;
				float: left;
				clear: both;
				display: block;
				width: 100%;
				margin-bottom: 5px; }
			textarea {
				display: block;
				width: 100%;
				box-sizing: border-box;
				height: 40px;
				border: 1px solid #e9e9e9;
				display: block;
				box-sizing: border-box;
				padding: 4px 5px; } }
		.row {
			width: 100%;
			float: left;
			clear: both; }
		.keyRefresh {
			float: right;
			display: block;
			font-family: 'Roboto' , sans-serif;
			font-size: 18px;
			color: #fff;
			font-weight: 300;
			background-color: #b01b52;
			border: 0;
			text-transform: uppercase;
			width: 185px;
			height: 50px;
			box-sizing: border-box;
			cursor: pointer;
			transition-duration: .5s;
			margin-top: 30px;
			&:hover {
				opacity: .9; } }
		.longField:nth-child(4), .longField:nth-child(7) {
			margin-right: 0; } }
	.selectField {
		span {
			width: 100% !important; } }
	.customSelect {
	    font: 14px;
	    background: #fff url('../images/icons/arrowBottom.png') right 10px center no-repeat;
	    border: 1px solid #ddd;
	    color: #555;
	    -moz-border-radius: 2px;
	    -webkit-border-radius: 2px;
	    border-radius: 2px 2px;
	    width: 340px;
	    height: 40px;
	    padding: 10px 10px 15px 10px;
	    box-sizing: border-box;
	    display: none;
	    font-family: 'Roboto' , sans-serif;
	    color: #999;
	    cursor: pointer;
	    border: 1px solid #e9e9e9;
	    background-color: #fff;
	    text-align: left; }
	.psevodCheckBox {
		width: 15px;
		height: 15px;
		border: 1px solid #e9e9e9;
		float: left;
		background-position: center;
		background-repeat: no-repeat;
		cursor: pointer;
		background-color: #fff;
		&[data-status="on"] {
			background-image: url('../images/icons/arrowPodpiska.png'); }
		&[data-status="off"] {}
		input {
			display: none; } }
	.line {
		margin-bottom: 10px;
		span {
			font-size: 14px;
			color: #000;
			font-weight: 300;
			font-family: 'Roboto' , sans-serif;
			padding-left: 10px; } } }
